<template>
  <section class="report-manager chart-container">
    <el-row>
      <el-col :span="12">
        <div id="chartColumn" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartBar" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartLine" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartPie" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="24">
        <a href="http://echarts.baidu.com/examples.html" target="_blank" style="float: right;">more>></a>
      </el-col>
    </el-row>
  </section>
</template>

<script>
  import echarts from 'echarts'

  export default {
    name: "reportmanager",
    data() {
      return {
        chartColumn: null,
        chartBar: null,
        chartLine: null,
        chartPie: null
      }
    },
    methods: {
      drawColumnChart() {
        this.chartColumn = echarts.init(document.getElementById('chartColumn'));
        this.chartColumn.setOption({
          title: {text: 'Column Chart'},
          tooltip: {},
          xAxis: {
            data: ["", "", "", "", "", ""]
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }],
          label: {
            normal: {
              show: true,
              position: 'top'
            }
          },
          itemStyle: {
            normal: {
              //colorListcolorList
              color: function (params) {
                var colorList = ['#4f81bd', '#c0504d', '#9bbb59', '#604a7b', '#948a54', '#e46c0b'];
                return colorList[params.dataIndex];
              }
            }
          }
        });
      },
      drawBarChart() {
        this.chartBar = echarts.init(document.getElementById('chartBar'));
        this.chartBar.setOption({
          title: {
            text: 'Bar Chart'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: ['2011', '2012']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          yAxis: {
            type: 'category',
            data: ['', '', '', '', '', '()']
          },
          label: {
            normal: {
              show: true,
              position: 'right'
            }
          },
          series: [
            {
              name: '2011',
              type: 'bar',
              data: [18203, 23489, 29034, 104970, 131744, 630230]
            },
            {
              name: '2012',
              type: 'bar',
              data: [19325, 23438, 31000, 121594, 134141, 681807]
            }
          ]
        });
      },
      drawLineChart() {
        this.chartLine = echarts.init(document.getElementById('chartLine'));
        this.chartLine.setOption({
          title: {
            text: 'Line Chart'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['', '', '']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['', '', '', '', '', '', '']
          },
          label: {
            normal: {
              show: true,
              position: 'top'
            }
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '',
              type: 'line',
              stack: '',
              data: [120, 132, 101, 134, 90, 230, 210]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [220, 182, 191, 234, 290, 330, 310]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [820, 932, 901, 934, 1290, 1330, 1320]
            }
          ]
        });
      },
      drawPieChart() {
        this.chartPie = echarts.init(document.getElementById('chartPie'));
        this.chartPie.setOption({
          title: {
            text: 'Pie Chart',
            subtext: '',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['', '', '', '', '']
          },
          label: {
            normal: {
              formatter: '{b}:{c}: ({d}%)',
              textStyle: {
                fontWeight: 'normal',
                fontSize: 15
              }
            }
          },
          // label:{            //
          //     normal:{
          //         show:true,
          //         position:'inner', //
          //         textStyle : {
          //             fontWeight : 300 ,
          //             fontSize : 16    //
          //         },
          //         formatter:'{d}%'
          //     }
          // },
          series: [
            {
              name: '',
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: [
                {value: 335, name: ''},
                {value: 310, name: ''},
                {value: 234, name: ''},
                {value: 135, name: ''},
                {value: 1548, name: ''}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });
      },
      drawCharts() {
        this.drawColumnChart()
        this.drawBarChart()
        this.drawLineChart()
        this.drawPieChart()
      },
    },

    mounted: function () {
      const that = this;
      that.drawCharts();
      window.onresize = function () {
        that.chartColumn.resize();
        that.chartBar.resize();
        that.chartLine.resize();
        that.chartPie.resize();
      };
    },
    updated: function () {
      this.drawCharts()
    }
  }
</script>

<style scoped>
  .chart-container {
    width: 100%;
    float: left;
  }

  .el-col {
    padding: 30px 20px;
  }
</style>



